export default {
	data() {
		return {
			date: '请选择日期',
			time: '请选择时间段'
		};
	},
	methods: {
		TimeChange(e) {
			this.time = e.detail.value
		},
		DateChange(e) {
			this.date = e.detail.value
		},
	}
}
